                            </div>
                            <small class="col-sm-5"></small>

                            <!-- <label v-if="forma.vista === false" class="col-sm-3 col-form-label">Entrada</label>
                            <div v-if="forma.vista === false" class="col-sm-9">
                                <container-componente :validator="$v.forma.entrada">
                                    <div class="custom-control custom-checkbox" >
                                        <input type="checkbox" class="custom-control-input" id="check-entrada" v-model="forma.entrada" @blur="$v.forma.entrada.$touch()">
                                        <label class="custom-control-label" for="check-entrada">Com Entrada</label>
                                    </div>
                                </container-componente>
                            </div> -->

                            <label v-if="forma.vista === false" class="col-sm-3 col-form-label">Máximo de Parcelas</label>
                            <div v-if="forma.vista === false" class="col-sm-3">
                                <input-componente v-model="forma.parcelas_maximo" :validator="$v.forma.parcelas_maximo" @blur="$v.forma.parcelas_maximo.$touch"/>
                            </div>
