<script>
import { FormaPagamentoService, PlanoContasService } from '../../../services'
import { required, maxLength, requiredIf, minValue} from "vuelidate/lib/validators";

const formaPagamentoService = new FormaPagamentoService();
const planoContasService = new PlanoContasService();

export default {
    data() {
        return {
            dados: [],
            contas: [],
            filtro:{
                ordenar: 'nome',
                ordem: 'asc',
                page: 1,
                per_page: 2000,
                texto: '',
                ativo: 'TODOS'
            },
            paginacao: {
                current_page: 0,
                last_page: 0,
                from: 0,
                to: 0,
                per_page: 2000
            },
            debounce: null,
            forma: null,
            sucessoDeletar: null,
            erroDeletar: null,
            sucessoEditar: null,
            erroEditar: null,    
            buscandoContas: null, 
            
        };
    },
    validations: {
         forma: {
            nome: { required, maxLength:maxLength(100)},
            descricao: {  maxLength:maxLength(200)},
            vista: { required },
            dias: { required, minValue: minValue(0) },
            caixa_id: {  },
            ativo: { required },
            compensa_automatico: {required},
            parcelas_maximo: { required: requiredIf( function() { return this.forma.vista === false; } )},
            entrada: { required: requiredIf( function() { return this.forma.vista === false; } )},
            taxa_sobre_total: {minValue:minValue(0)},
            taxa_sobre_total_forma: { required: requiredIf( function() { return this.forma.taxa_sobre_total >= 0.01; } )},
            taxa_sobre_total_cobranca :{ required: requiredIf( function() { return this.forma.taxa_sobre_total >= 0.01; } )},

            taxa_sobre_transacao: {minValue:minValue(0)},
            taxa_sobre_transacao_forma: { required: requiredIf( function() { return this.forma.taxa_sobre_transacao >= 0.01; } )},
            taxa_sobre_transacao_cobranca :{ required: requiredIf( function() { return this.forma.taxa_sobre_transacao >= 0.01; } )},
            
         }
    },
    mounted() {
        
        setTimeout(() => {
            this.buscar();
        }, 100);

    },
    methods: {

        abrirModalEditar:function(obj){
            this.buscarContas('');
            this.$v.forma.$reset();
            this.sucessoEditar = null;
            this.erroEditar = null;
            if(obj == null)
                this.forma = {dias: 0};
            else
                this.forma = {...obj};
        },

        salvar: function(){
            this.sucessoEditar = null;
            this.erroEditar = null;

            this.$v.forma.$touch();
            if (this.$v.forma.$invalid){
                // this.erroEditar = this.$v.forma;
                this.erroEditar = 'Preencha os campos obrigatórios';
                return false;
            }

            
            if(!this.forma.vista)
                this.forma.compensa_automatico = false;
            this.forma.entrada = false;

            let req = this.forma.id ? formaPagamentoService.atualizar(this.forma) : formaPagamentoService.criar(this.forma);

            req.then( response => {
                let obj = response.data;
                
                let i = this.dados.map(x => x.id).indexOf(obj.id);
                if(i > -1)
                    this.dados.splice(i,1, obj);
                else
                    this.dados.push(obj);                
                
                this.sucessoEditar = 'Conta salva com sucesso!';
            }).catch( erro => this.erroEditar = erro)            
        },

        deletar: function(){
            this.sucessoDeletar = null;
            this.erroDeletar = null;

         formaPagamentoService.deletar(this.forma.id)
            .then( response => {
                let i = this.dados.map(x => x.id).indexOf(this.forma.id);
                this.dados[i].ativo = false;
                this.sucessoDeletar = 'Registro removido com sucesso!';
            }).catch( erro => this.erroDeletar = erro)            
        },

        debounceBusca: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.navegar(1);
            }, 800);
        },
        
        buscar:function(){
            // if(this.dados.length > 0)
            //     return this.filtrar();
            formaPagamentoService.listar(this.filtro).then(
                    response => {
                        this.$data.dados = response.data.data.map(x => {
                            if(x.caixa)
                                x.caixa.label = `${x.caixa.codigo} | ${x.caixa.nome}`;
                            return x;
                        })
                    }
                )
        },

        navegar: function(pagina){
            this.filtro.page = pagina;
            this.buscar();
        },


        buscarContasDebounce: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.buscarContas();
            }, 800);
        },
        
        buscarContas:function(){
            this.buscandoContas = true;
            planoContasService.listar({tipo: "TRANSFERENCIA", per_page: 50}).then(
                response => {
                    this.contas = response.data.data.map(x => {
                        return {id: x.id, label: `${x.codigo} | ${x.nome}`}
                    });                    
            }).finally( () => this.buscandoContas = false)
        },
    
    },
    computed:{
        preco(){
            return {
                decimal: ',',
                thousands: '.',
                prefix: '',
                suffix: '',
                precision: 2,
                masked: false                   

            }
        },
        inteiro(){
            return {
                decimal: ',',
                thousands: '.',
                prefix: 'D+ ',
                suffix: '',
                precision: 0,
                masked: false                   

            }
        },
    }
};
</script>
<style scoped>
    tr:hover{
        background-color: #eff2f7 ;
    }
</style>
